@value scrollerSidePadding: 40px;
@value dashSideMargin: 5px;
@value dashWidth: 50px;
.timelineContainer {
	-webkit-box-shadow: inset 0px 10px 15px -10px  rgba(0, 0, 0, 0.2);
	        box-shadow: inset 0px 10px 15px -10px  rgba(0, 0, 0, 0.2);
	overflow: hidden;
}

.scrollerContainer {
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	display: -ms-inline-flexbox;
	display: inline-flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	background-color: rgb(227, 234, 255);
	height: 100%;
	cursor: pointer;
	-ms-flex-pack: center;
	    justify-content: center;
	-webkit-box-shadow: inset 0px 10px 15px -10px  rgba(0, 0, 0, 0.2);
	        box-shadow: inset 0px 10px 15px -10px  rgba(0, 0, 0, 0.2);
}

.scrollerGridContainer{
  display: grid;
  margin-left: scrollerSidePadding;
	grid-template-rows: 200px auto;
}

.scrollTitleContainer {
  grid-row: 1/2;
}

.scrollerBodyContainer {
  grid-row: 2/3;
	padding-left: scrollerSidePadding;
	display: -ms-inline-flexbox;
	display: inline-flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-direction: row;
	    flex-direction: row;
	cursor: pointer;
}

.dashedLine {
  grid-row: 2/3;
	margin: auto dashSideMargin;
	width: dashWidth;
	border: 0px;
	border-top: dashed 1px;
	color: #7494ea;
}

.fullSize {
	width: 100%;
	height: 100%;
}



