@value padding: 10px;
.bookContainer,
.selectedBookContainer {
  -webkit-transition: all 0.1s ease;
  -o-transition: all 0.1s ease;
  transition: all 0.1s ease;
	background-color: #7494ea;
	grid-row: 2/3;
}

.bookSelector {
	-webkit-transition: all 0.1s ease;
	-o-transition: all 0.1s ease;
	transition: all 0.1s ease;
	margin: padding;
	background-color: #7494ea;
	cursor: pointer;
	color: white;
	font-size: 1.3em;
	text-align: center;
	white-space: nowrap;
}

.bookContainer:hover {
	-webkit-transition: all 0.2s ease;
	-o-transition: all 0.2s ease;
	transition: all 0.2s ease;
	-webkit-transform: scale(1.1);
	    -ms-transform: scale(1.1);
	        transform: scale(1.1);
	background-color: rgb(86, 124, 230);
}
.bookContainer:hover .bookSelector {
	-webkit-transition: all 0.2s ease;
	-o-transition: all 0.2s ease;
	transition: all 0.2s ease;
	background-color: rgb(86, 124, 230);
}

.bookArrayContainer {
	display: -ms-flexbox;
	display: flex;
}

.bookSection {
	-ms-flex: 1 1;
	    flex: 1 1;
	/* TODO: Make height dynamic with rest of the sections */
	height: 44.8px;
	border-right: 1px solid rgba(0, 0, 0, 0.15);
	position: relative;
}

.bookSection:last-child {
	border-right: 0px;
}

.bookSection:hover {
	-webkit-transition: all 0.1s ease;
	-o-transition: all 0.1s ease;
	transition: all 0.1s ease;
	background-color: rgba(0, 0, 0, 0.466);
}
.bookSection:hover .bookNumber {
	-webkit-transition: all 0.05s ease;
	-o-transition: all 0.05s ease;
	transition: all 0.05s ease;
	opacity: 1;
}

.bookNumber {
	background-color: rgba(0, 0, 0, 0.466);
	-webkit-transition: all 0.2s ease;
	-o-transition: all 0.2s ease;
	transition: all 0.2s ease;
	top: -25px;
	left: 50%;
	-webkit-transform: translateX(-50%);
	    -ms-transform: translateX(-50%);
	        transform: translateX(-50%);
	position: absolute;
	opacity: 0;
	/* Disable hover on child, alternatively make another container */
	pointer-events: none;
}



