@value borderSideHeight: 20px;

.scrollerSummaryContainer {
	display: grid;
	grid-auto-flow: row;
	grid-column: 1fr;
	grid-auto-rows: -webkit-max-content;
	grid-auto-rows: max-content;
	height: 100%;
	background-color: rgb(184, 201, 253);
	-webkit-box-shadow: inset 0px 1px 17px 0px #00000036;
	        box-shadow: inset 0px 1px 17px 0px #00000036;
}

.mainGuideline {
	height: borderSideHeight;
	border: 2px solid #7494ea;
	border-top: none;
}

.secondaryGuideline {
	height: 100%;
	border-right: 2px dotted #7494ea;
	border-left: 2px dotted #7494ea;
	grid-column: 1/2;
}

.titleContainer{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
}

.title {
	-webkit-transition: 0.2s all ease;
	-o-transition: 0.2s all ease;
	transition: 0.2s all ease;
	display: block;
	text-align: center;
	overflow-wrap: break-word;
	text-decoration: none;
	color: #0035c3;
}

.title:hover {
	-webkit-transition: 0.2s all ease;
	-o-transition: 0.2s all ease;
	transition: 0.2s all ease;
	-webkit-transform: scale(1.2);
	    -ms-transform: scale(1.2);
	        transform: scale(1.2);
}



