a {
  margin: 0px;
}

.container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: justify;
	    justify-content: space-between;
  -ms-flex-align: end;
      align-items: flex-end;
  background-color: #50576b;
  border-radius: 5%;
  padding: 10px 5px;
}

.icon {
  color: #2e323d;
  padding: 1px;
	background-color: #ff7d4c;
	border-radius: 50%;
	margin-right: 5px;
}

.clickableAction {
	opacity: 0.75;
	cursor: pointer;
	text-shadow: 0 0 1px white;
	font-size: 0.85em;
}

.clickableAction:hover {
	-webkit-transition: 0.1s all ease;
	-o-transition: 0.1s all ease;
	transition: 0.1s all ease;
	opacity: 1;
	text-decoration: underline;
}



