.container {
	background-color: white;
	color: #6b8ce4;

	display: -ms-flexbox;

	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

h2 {
  margin-left: 20px;
  font-size: 1.5em;
}

.rightContainer {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	margin-right: 25px;
}

.rightContainer > a {
  color: rgb(89, 126, 230);
	text-decoration: none;
	margin-right: 20px;
}

.rightContainer > a:last-child {
	margin-right: 0px;
}

.rightContainer > a:hover {
	color: #1643be;
}



